<template lang='pug'>
  #app(style='height:2000px;')
    button.btn.btn-primary.btn-block.w-25(type='button', @click='showAlertBox') AlertBox
    button.btn.btn-primary.btn-block.w-25(type='button', @click='showConfirmBox') ConfirmBox
    button.btn.btn-primary.btn-block.w-25(type='button', @click='showPromptBox') PromptBox
    cf-alert-box(ref='alert-box' confirmText='Save changes')
    cf-confirm-box(ref='confirm-box' notConfirmText='Close' confirmText='Save changes' @confirm='confirm')
    cf-prompt-box(ref='prompt-box' modalType='modal-md' notConfirmText='Close' confirmText='Save changes' @confirm='confirm')
      .form-inline(slot='body')
        select.form-control.w-100
          option option 1
          option option 2
</template>

<script>
import 'bootstrap/dist/css/bootstrap.min.css'
import '../src/styles/index.scss'

export default {
  name: 'app',
  methods: {
    showAlertBox () {
      this.$refs['alert-box'].show(
        'Modal title',
        'Modal body text goes here.',
      )
    },
    showConfirmBox () {
      this.$refs['confirm-box'].show(
        'Modal title',
        'Modal body text goes here.',
        () => console.log('This is confirm box.')
      )
    },
    confirm (callback) {
      callback && callback()
    },
    showPromptBox () {
      this.$refs['prompt-box'].show('Modal title')
    }
  }
}
</script>
